import React,{ useState } from 'react';
import axios from 'axios';
import './login.css';
import { getAuth, signInWithEmailAndPassword,signOut } from "firebase/auth";
import { auth } from '../../firebase-context';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Routes
  } from 'react-router-dom';
import Loader from "react-loader";
import { useAuth } from '../../contexts/AuthContext';

function Login(props){
        
let [username,setusername]=useState('');
let [password,setpassword]=useState('');
let [errMessage,seterrMessage]=useState('');
let [users,setusers]=useState('');
let [loading,setloading]=useState(false);

var [currentUser,setCurrentUser]=useState()

var options = {
    lines: 13,
    length: 20,
    width: 10,
    radius: 30,
    scale: 1.00,
    corners: 1,
    color: '#000',
    opacity: 0.25,
    rotate: 0,
    direction: 1,
    speed: 1,
    trail: 60,
    fps: 20,
    zIndex: 2e9,
    top: '50%',
    left: '50%',
    shadow: false,
    hwaccel: false,
    position: 'absolute'
};
   

    var handleSubmit = async(e) => {
    setloading(true);
    e.preventDefault()
    try {
        const user = await signInWithEmailAndPassword(
          auth,
          username,
          password
        );  
        console.log(user);
        
        setloading(false);
        props.loginCall(true,user);
      } catch (error) {
        
        console.log(error.message);
        props.loginCall(true,"");
        
    setloading(false);
      }
      
      //window.location.pathname="/";
    // let requestBody={
    //     name: username,
    //     password: password
    //   }
    //     e.preventDefault();

    //     fetch("http://localhost:8080/users/login",{
    //         method:'POST', 
    //         headers: {
    //             'Content-Type': 'application/json'
    //         },
    //         body: JSON.stringify(requestBody)
    //     }).then(res=>res.json())
    //   .then(
    //     (result) => {
    //         console.log(result.stat)
    //         this.setState({
    //             users:result
    //         })
    //     },
    //     (error) => {
    //         console.log(error)
    //     }
    //   )
   
        
    }


        return (
            <div className="bodyContainer">
                <Loader loaded={!loading} options={options} className="spinner" />
                <div className="container1">
                    <div Id="detailsbg">
                        
                        <form onSubmit={handleSubmit}>
                            <div Id="details">
                                <div Id="loginImage"></div>
                                <h2 Id="loginHeading">Login</h2>
                                <label className="inputBlock">
                                    <span>Username</span>
                                    <input className="w-full" type="text" name="username" required={true}
                                    value={username} onChange={(e)=>setusername(e.target.value)} 
                                    />
                                </label>
                                <label className="inputBlock ">
                                    <span>Password</span>
                                    <input className="w-full" type="password" name="password" required={true}
                                    value={password} onChange={(e)=>setpassword(e.target.value)} 
                                    />
                                </label>
                            </div>
                            <div className="errInfo">{errMessage}</div>
                                <input type="submit" value="Log in" className="buttonStyle" />
                        </form>
                        <div Id="loginButton">
                            <a href="/signup" className="link algCenter">Create Account</a>
                            <a href="/" className="link gray algCenter">Forgot Password?</a>
                        </div>
                    </div>
                    <div Id="loginImagebg"></div>
                </div>
            </div>
        )
    
}

export default Login