import React from 'react';
import './experts.css';
import ExpertCard from '../ExpertCard/expertCard';
import {mockdata} from './mock.js'

function Experts () {
  var mock=mockdata.slice(0,3)
  console.log(mockdata,mock)
    return <div className="main">

      <form style={{width: '100%', padding: '0.3em 3em', margin: '1em 0'}}>
          <div style={{width: '90%', display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}}>
              <label>
                  <input className="" type="text" name="username" required={true}
                  placeholder=""/>
              </label>
            <input type="submit" value="Search" style={{cursor:"pointer"}} />
          </div>
      </form>

      <div className="container2">
      {
        mockdata.map((moc)=>
          <ExpertCard 
            id={moc.id}
            image={moc.image}
            first_name={moc.first_name}
            last_name={moc.last_name}
            gender={moc.gender}
            email={moc.email}
            designation={moc.designation}
          />
        )
      }

      </div>
    </div>
}
export default Experts;