
import './App.css';
import About from './Components/About/about';
import Contact from './Components/Contact/contact';
import Blogs from './Components/Blogs/blogs';
import Experts from './Components/Experts/experts';
import SelfTest from './Components/SelfTest/selftest';
import Home from './Components/Home/home';
import Login from './Components/Login/login';
import Signup from './Components/Signup/signup';
import Book from './Components/Book/book';
import Membership from './Components/Membership/membership';
import { auth } from './firebase-context';
import React, { useState } from "react";
import { getAuth, signInWithEmailAndPassword,signOut } from "firebase/auth";
import Style from 'style-it';

import {
  BrowserRouter as Router,
  Route,
  Link,
  Routes,
  Switch,
  Navigate
} from 'react-router-dom';
import { AuthProvider } from './contexts/AuthContext';
import PrivateRoute from './PrivateRoute';

function App() {
  let [menus, setMenus] = useState();
  let [closeText,setCloseText]=useState();
  let [x,setx]=useState();
  let [y,sety]=useState();
  var [loggedIn,setLoggedIn]=useState();
  var [currentUser,setCurrentUser]=useState();
var menuChange=()=>{
  setMenus(!menus)
  setCloseText('Close')
  // setTimeout(()=>{
  //   setCloseText('Close')
  // },1000);
}
var closeChange=()=>{
  setMenus(!menus)
  setCloseText('')
  // setTimeout(()=>{
  //   setCloseText('')
  // },100);
}
var mousemoved=(e)=>{
  const y=e.pageX - e.target.offsetLeft;
  const x=e.pageY - e.target.offsetTop;
  setx(x+'px');
  sety(y+'px');
  
}

var logout=async(e)=>{
  //setloading(true);
  console.log("auth",auth.currentUser)
  e.preventDefault()
  await signOut(auth)
  
  setLoggedIn(false)
  setCurrentUser(null)
  console.log("auth",auth.currentUser)
  //setloading(false);
}
var handleLogin=(loginData,user)=>{
  console.log("in")
  setLoggedIn(loginData)
  setCurrentUser(user)
}
  return Style.it(`
  
.login{
  display: inline-flex;
  justify-content:center;
  align-items:center;
  cursor:pointer;
  padding: 5px 10px;
  color: black;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  width: 12vmin;
  height: 5vmin;
  margin-right:1vw;
  margin-bottom:1vh;
  box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
}
.login::before{
  content: '';
  position: absolute;
  top:${x};
  left:${y};
  transform: translate(-50%,-50%);
  width: 0px;
  height: 0px;
  border-radius: 50%;
  background: #04c10a;
  transition: width 1s,height 1s;
}
.login:hover::before{
  width: 20vw;
  height: 20vw;
}
.login span{
  z-index:1;
}
  `,
    <div className="App">
        <Router>
           <div>
             {menus && <div className='menuDivActive'>
            {/* <button className="closeButton" onClick={closeChange}>{closeText}</button> */}
            <div className="links">
            {/* <Link className="link" onClick={closeChange} to="/experts">Experts</Link>
            <Link className="link" to="/selftest">Self Test</Link> 
            <Link className="link" to="/blogs">Blogs</Link> */}
            {/* <Link className="link" to="/about">About Us</Link>
            <Link className="link" to="/contact">Contact Us</Link>  */}
            </div>
            </div>}
            <div className="nav">
            {/* <button className="menuButton" onClick={menuChange}>Menu</button> */}
            <div>
            <Link className="experts" to="/experts">Experts</Link>
            <Link className="experts" to="/selftest">Selftest</Link> 
            <Link className="experts" to="/blogs">Blogs</Link>
            </div>
            <div>
            <Link className="home" to="/">Home</Link>
            </div>
            <div>
            {!loggedIn && <Link className="glassbutton" onMouseMove={mousemoved} to="/login"><span>Login</span></Link>}
            {/* {!loggedIn && <Link className="login" onMouseMove={mousemoved} to="/signup"><span>Signup</span></Link>} */}
            {loggedIn && <button className='glassbutton' onMouseMove={mousemoved} onClick={logout}><span>Logout</span></button>}
            </div>
            </div>
            <Routes>
              <Route exact path='/book' element={currentUser ? <Book /> : <Navigate to="/login" />}></Route>
              <Route exact path='/membership' element={currentUser ? <Membership /> : <Navigate to="/login" />}></Route>
              <Route exact path='/experts' element={currentUser ? <Experts /> : <Navigate to="/login" />}></Route>
              <Route exact path='/selftest' element={currentUser ? <SelfTest /> : <Navigate to="/login" />}></Route>
              <Route exact path='/blogs' element={currentUser ? <Blogs /> : <Navigate to="/login" />}></Route>
              <Route exact path='/login' element={currentUser ? <Navigate to="/"/> : <Login loginCall={handleLogin}/>}></Route>
              {/* <Route exact path='/signup' element={<Signup/>}></Route> */}
              <Route exact path='/' element={currentUser ? <Home  /> : <Navigate to="/login" />}></Route>
              
              {/* <Route exact path='/' element={<Home/>}></Route> */}

            </Routes>
            {/* <div className="appointMain">
            <Link className="appoint" to="/Appointment">
            <svg viewBox="0 0 25 25"><path d="M7 12.5 17.293 2l.707.707L8.414 12.5 18 22.293l-.707.707L7 12.5z"/></svg>
            <span>Book An Appointment</span> 
            </Link>
            <Link className="appoint" to="/Membership">
            <span>Membership</span> 
            <svg viewBox="0 0 25 25"><path d="M18 12.5 7.707 23 7 22.293l9.586-9.793L7 2.707 7.707 2 18 12.5z"/></svg>
            </Link>
            </div> */}
           </div>
       </Router>
    </div>
  );
}

export default App;
